import Vue from 'vue'
import VueRouter from 'vue-router'
import Manager from '../views/Manager/index.vue'
import store from '@/store'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes:[
    {
      path:'/login',
      name:'login',
      // redirect:'/login',
      component:()=>import('@/views/Login/index.vue'),
    },
    {
      path:'/',
      component:()=>import('@/views/Manager/index.vue'),
      redirect:'/home',
      children:[
        {
          path:'articles',
          name:'Articles',
          component:()=>import('@/views/Manager/Articles/index.vue'),
          meta:{name:'内容管理'}
        },
        {
          path:'herbs',
          name:'herbs',
          component:()=>import('@/views/Manager/Herbs'),
          meta:{name:'草药管理'}
        },
        {
          path:'herbsapproval',
          name:'herbsapproval',
          component:()=>import('@/views/Manager/HerbApproval'),
          meta:{name:'草药审批'}
        },
        {
          path:'articlesapproval',
          name:'articlesapproval',
          component:()=>import('@/views/Manager/ArticlesApproval/index.vue'),
          meta:{name:'审批管理'}
        },
        {
          path:'home',
          name:'Home',
          component:()=>import('@/views/Home/index.vue'),
          meta:{name:'首页'}
        },
        {
          path:'users',
          name:'Users',
          component:()=>import('@/views/Manager/Users/index.vue'),
          meta:{name:'用户管理'}
        },
        {
          path:'application',
          name:'Application',
          component:()=>import('@/views/Manager/Application/index.vue'),
          meta:{name:'管理员审核'}
        },
        {
          path:'permission',
          name:'Permission',
          component:()=>import('@/views/Manager/permission/index.vue'),
          meta:{name:'管理员申请'}
        },
      ]
    },
    {
      path:'*',
      redirect:'/login'
    }
  ]
})


//白名单（无需登录就可以访问的地址）
let whiteList = ['/login']
//全局前置路由守卫
router.beforeEach((to,from,next)=>{
  const token = store.state.token
  if(token){  //登录了
    if(token && !store.state.userInfo.username){
      store.dispatch('getUserInfoActions')
    }
    next()
  }else{
    //未登录
    if(whiteList.includes(to.path)){
      //未登录
      next()
    }else{
      next('/login')
    }
  }
})

export default router
