<template>
  <div>
    <div style="padding:10px 0;">
      <el-input type="text" style="width:240px;" placeholder="请输入想要搜索的内容" suffix-icon="el-icon-search"></el-input><el-button type="primary" style="margin-left:5px;">搜索</el-button>
    </div>
    <el-table :data="articles" border stripe :header-cell-class-name="headerBg">
      <el-table-column prop="id" label="序号" width="50" align="center">
      </el-table-column>
      <el-table-column prop="title" label="文章标题" width="150">
      </el-table-column>
      <el-table-column prop="pic" label="文章图片">
        <template slot-scope="{row,$index}">
          <img :src="'http://8.130.20.53/image' + row.pic[0]" alt="" style="width:100px;">
        </template>
      </el-table-column>
      <el-table-column prop="authorId" label="文章作者ID" width="150">
      </el-table-column>
      <el-table-column prop="likes" label="文章获赞量" width="150">
      </el-table-column>
      <el-table-column prop="" label="操作">
        <template slot-scope="{row,$index}">
          <el-button type="success" style="padding:7px 10px;font-size:10px;" @click="editArticle(row)">编辑<i class="el-icon-edit-outline" style="padding-left:2px;"></i></el-button>
          <el-button type="danger" style="padding:7px 10px;font-size:10px;" @click="deleteArticles(row)">删除<i class="el-icon-delete" style="padding-left:2px;"></i></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- ============================================= -->
    <el-dialog title="审核文章" :visible.sync="dialogFormVisible">
      <el-form style="width:80%;" :model="currentArticles">
        <el-form-item label="文章标题" label-width="100px">
          <el-input v-model="currentArticles.title" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="文章概述" label-width="100px">
          <el-input v-model="currentArticles.articledesc" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="文章正文" label-width="100px">
          <quill-editor v-model="currentArticles.content"></quill-editor>
        </el-form-item>
        <el-form-item label="文章标签" label-width="100px">
        <el-select placeholder="请选择文章标签" value="">
            <el-option label="区域一" value="shanghai"></el-option>
            <el-option label="区域二" value="beijing"></el-option>
        </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateFn()">修 改</el-button>
      </div>
    </el-dialog>

    <!-- ============================================= -->

    <div style="padding:10px 0;">
      <el-pagination
        @current-change="handleCurrentChange"
        @size-change="handleSizeChange"
        style="text-align:center"
        :page-sizes="pageSizes"
        :page-size="pageSize"
        :currentPage="currentPage"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name:'Articles',
  data(){
      return{
          articles:[],
          total: 0,
          currentPage: 1,
          pageSize: 5,
          pageSizes: [5,7,10],
          headerBg:'headerBg',
          dialogFormVisible:false,
          imageUrl:'',
          //收集到的当前点击的信息
          currentArticles:[]
      }
  },
  mounted(){
    this.getArticlesLength()
    this.getArticles()

  },
  methods:{
    //编辑文章内容
    editArticle(row){
      this.currentArticles = row
      this.dialogFormVisible = true
    },
    //删除文章
    deleteArticles(row){
      this.currentArticles = row
      console.log(this.currentArticles.id);
      this.$confirm('此操作将永久删除该文章，是否继续?','提示',{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(()=>{
        this.$axios.delete('/admin/deleteArticle',{
          params:{
            id: this.currentArticles.id
          }
        }).then(res=>{
          if(res.status == 200){
            this.$message({
              type:'success',
              message:'删除成功'
            })
          }
          this.getArticles()
          this.getArticlesLength()
        })
      }).catch(()=>{
        this.$message({
          type:'info',
          message:'已取消删除'
        })
      })
    },
    //获取文章总条数
    getArticlesLength(){
      this.$axios.get('/admin/getUserArticlesLength',{
        headers:{
          Authorization: this.$store.state.token
        }
      }).then(res=>{
        if(res.status == 0){
          console.log(res.data[0].length);
          this.total = JSON.parse(res.data[0].length)
        }
      })
    },
    //获取文章数据
    getArticles(){
      const token = localStorage.getItem('token')
      this.$axios.get('/admin/getUserArticles',{
        headers:{
          Authorization: this.$store.state.token
        },
        params:{
            pageSize:this.pageSize,
            currentPage:this.currentPage,
        },
      }).then(res=>{
        if(res.status == 200){
          this.articles = res.data
        }
      })
    },
    //更新文章内容
    updateFn(){
      this.dialogFormVisible = false;
      this.$axios.post('/admin/updateArticleInfo',{
        currentArticleInfo:this.currentArticles,
        id:this.currentArticles.id}
        ).then(res=>{
        if(res.status == 200){
          this.currentArticlesInfo = res.data
          this.$message({
            type: 'success',
            message: '修改成功，文章将重新等待审批！'
          })
        }
      })
    },
    handleCurrentChange(pager){
      this.currentPage = pager
      this.getArticles()
    },
    handleSizeChange(limit){
      this.pageSize = limit
      this.getArticles()
    }
  }
}
</script>

<style>
.headerBg{
  background: #ddd !important;
}
.el-main{
  /* overflow: hidden; */
  padding-top: 0;
}
/* 上传图片 */
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>